import { Range, InlineCompletionItem, TextDocument, TextEditor, SnippetString } from "vscode";
import { TelemetryMeasureNames, TelemetryPropertyNames } from "./telemetry";

/**
 * Post processing of our suggestions need to return two results.
 * One to return to VSCode, and one that has necessary snippet notations.
 */
export interface PostprocessResults {
  /**
   * Completion processed for visualization.
   * ex) onsole.log(<STR_LIT>);<endofline> becomes
   *     onsole.log("");
   */
  completionFromCacheText: string;

  /**
   * Completion processed w/ necessary VSCode snippet notations.
   * ex) onsole.log(<STR_LIT>);<endofline> becomes
   *     onsole.log("${1}");${0}
   */
  snippetCompletionText: string;

  /**
   * Number of snippet notations inserted into the snippetCompletionText.
   */
  snippetCount: number;
}

export type FullyProcessedCompletionData = {
  /**
   * The string to be returned back to VSCode.
   * Takes care of discrepancy between results from cache and results VSCode expects.
   */
  completionToReturn: SnippetString;

  /**
   * The snippet notation string to overwrite completionToReturn with.
   */
  snippetToReturn: string;

  additionalData: {
    /**
     * What the current line would look like after the completion has been accepted.
     * Use to validate legitimacy of the completion.
     */
    fullLineWithCompletion: string;

    /**
     * What the whole line with snippets would be.
     * Use to validate legitimacy of the completion with snippets.
     */
    fullLineWithSnippet: string;

    /**
     * The range of the completion once it has been accepted.
     * Use to keep track of what range our completion affects once accepted.
     */
    postAcceptanceRange: Range;
    /**
     * The range of text to be replaced by the inline completion.
     * This is passed to VS Code
     */
    completionRange: Range;

    /**
     * Number of snippet placeholders in the snippet.
     */
    snippetCount: number;

    /**
     * Post processed completion from cache
     */
    completionFromCacheText: string;
  };
};

export type ModifiedCompletionResult = {
  /**
   * Modified text to be returned when walking the prediction
   */
  text: string;

  /**
   * Modified range to be returned when walking the prediction
   */
  range: Range;
};

/**
 * Completion item last provided to the user
 */
export interface PreviousCompletionItemProvided {
  /**
   * Code in line upto the cursor
   */
  codeTillCursor: string;
  /**
   * Full line with previous completion. This is buffer text + completion text.
   */
  fullLineCompletion: string;
  /**
   * The previous completion item
   */
  completionItem: ExtendedInlineCompletionItem;
  /**
   * The document on which the last completion was provided
   */
  document: TextDocument;
  /**
   * The line on which the last completion was provided
   */
  line: number;
  /**
   * Full line with previous completion with snippet
   */
  snippetString: string;
  /**
   * Range to replace of the snippet completion
   */
  snippetRange: Range;
  /**
   * The Intellisense item selected when the last completion was provided
   */
  selectedCompletion: string | undefined;
}

export type postAcceptanceCommandArguments = {
  /**
   * Was telemetry for this prediction sent?
   */
  wasTelemetrySent: boolean;
  /**
   * Adding a tag because this is a custom type, this is used in tests
   * Object.toString will return the value in this field
   */
  [Symbol.toStringTag]: "PostAcceptanceCommandArgumentsTag";
  /**
   * Properties for the completion item
   */
  properties: CompletionItemProperties;
  /**
   * Measures for the completion item
   */
  measures: CompletionItemMeasures;
};

/**
 * An InlineCompletionItem which has been extended to hold extra metadata,
 * particularly telemetry properties and measures to send if it is shown.
 */
export class ExtendedInlineCompletionItem extends InlineCompletionItem {
  /**
   * Should send telemetry or not.
   * If walking the prediction or completion driven, this should be set to false to avoid sending duplicate telemetry
   */
  shouldSendTelemetry: boolean = true;
  /**
   * Properties for the completion item
   */
  properties?: Partial<Record<TelemetryPropertyNames, any>>;
  /**
   * Measures for the completion item
   */
  measures?: Partial<Record<TelemetryMeasureNames, number>>;
}

export type CompletionItemProperties = {
  /**
   * The language id
   */
  languageId: string;
  /**
   * Unique identifier per request to inlineCompletionProvider
   */
  correlationGuid: string;
  /**
   * Triggering character
   * This can be a specific character typed or a specific scenario such as intellisense menu selection
   */
  triggeringCharacter: string | undefined;
};

export type CompletionItemMeasures = {
  /**
   * length of completion provided
   */
  completionLength: number;
  /**
   * Confidence score of first token of the completion
   */
  firstTokenScore: number;
  /**
   * Cumulative score of last token of the completion
   */
  cumulativeLastTokenScore: number;
  /**
   * Number of tokens in completion
   */
  tokenCount: number;
  /**
   * Penalty applied to last token score
   */
  lengthPenalizedLastTokenScore: number;
  /**
   * Number of snippet placeholders in completion
   */
  snippetCount: number;
  /**
   * First token confidence threshold
   */
  firstTokenConfidenceThreshold: number;
  /**
   * If experimental triggering characters were used
   * 0 if false, 1 if true
   */
  expTriggeringCharacters: number;
  /**
   * Time taken from when completion provider is called to when completion is sent to the user
   */
  latencyMilliseconds: number;

};

export function createPropertiesRecord(properties: CompletionItemProperties) {
  return {
    [TelemetryPropertyNames.LANGUAGE_ID]: properties.languageId,
    [TelemetryPropertyNames.CORRELATION_GUID]: properties.correlationGuid,
    [TelemetryPropertyNames.TRIGGERING_CHARACTER]: properties.triggeringCharacter,      
  };
}

export function createMeasuresRecord(measures: CompletionItemMeasures) {
  return {
    [TelemetryMeasureNames.COMPLETION_LENGTH]: measures.completionLength,
    [TelemetryMeasureNames.FIRST_TOKEN_SCORE]: measures.firstTokenScore,
    [TelemetryMeasureNames.CUMULATIVE_LAST_TOKEN_SCORE]: measures.cumulativeLastTokenScore,      
    [TelemetryMeasureNames.TOKEN_COUNT]: measures.tokenCount,
    [TelemetryMeasureNames.LENGTH_PENALIZED_LAST_TOKEN_SCORE]: measures.lengthPenalizedLastTokenScore,
    [TelemetryMeasureNames.SNIPPET_PLACEHOLDER_COUNT]: measures.snippetCount,
    [TelemetryMeasureNames.FIRST_TOKEN_CONFIDENCE_THRESHOLD]: measures.firstTokenConfidenceThreshold,
    [TelemetryMeasureNames.EXP_TRIGGERING_CHARACTERS]: measures.expTriggeringCharacters,      
    [TelemetryMeasureNames.EVENT_DURATION]: measures.latencyMilliseconds,
  };
}
