import { MessagePort, parentPort, threadId } from "worker_threads";
import { ILoggingService, LogLevel } from "../services/logging";
import { LocalLineCompletionService } from "../services/completion";
import { CachingResultMessage, Message, ResponseMessage} from "./messages";
import { WorkerThreadCompletionResultCallback } from "./remotedServices/completionResultCallback";
import { WorkerThreadLoggingService } from "./remotedServices/logging";
import { WorkerThreadSettingsStorage } from "./remotedServices/settings";
import { WorkerThreadTelemetryService } from "./remotedServices/telemetry";
import Settings from "../services/settings/settings";
import { createLineCompletionServiceWithRetriesAsync } from "../services/completion/localLineCompletionService";
import { ICompletionResultCallback } from "../resultCache/completionResultCallback";
import { AwaitableMessageExchanger } from "../utils/awaitableMessageExchanger";
import { TelemetryEventNames, TelemetryPropertyNames } from "../@types/telemetry";

/**
 * Persistent state needed in the inference worker thread.
 */
class InferenceWorker {
  private responseCacheResultExchanger: AwaitableMessageExchanger<ResponseMessage, CachingResultMessage>;
  private completionState: ICompletionResultCallback;

  /**
   *
   * @param port Port which is used to communicate with the main extension host process
   * @param logger Logging for this worker.
   * @param lineCompletionService Line completion service to run within the worker.
   * @param completionState Completion callback which the line completion service will call when it has predictions.
   * @param settingCache Setting storage which requests settings from the main extension host, and caches the result.
   */
  constructor(
    private port: MessagePort,
    private logger: ILoggingService,
    private lineCompletionService: LocalLineCompletionService,
    private settingCache: WorkerThreadSettingsStorage
  ) {
    this.responseCacheResultExchanger = new AwaitableMessageExchanger<ResponseMessage, CachingResultMessage>(m => port.postMessage(m));
    this.completionState = new WorkerThreadCompletionResultCallback(this.responseCacheResultExchanger);
    port.on("message", (message) => {
      this.handleMessage(message);
    });
    port.once("close", () => {
      parentPort?.close();
    });
  }

  /**
   * Processes incoming messages and takes appropriate actions.
   * @param msg Incoming message to process
   */
  private handleMessage(msg: Message) {
    try {
      switch (msg.kind) {
        case "RequestMessage":
          this.lineCompletionService.request(
            msg.requestId,
            msg.normalizedContext,
            msg.lang,
            this.completionState,
            { isCancellationRequested: false },
            msg.correlationGuid,
          );
          break;
        case "SettingsResponseMessage":
          this.settingCache.handleUpdateResponse(msg);
          break;
        case "CachingResultMessage":
          this.responseCacheResultExchanger.handleReceivedMessage(msg);
          break;
        default:
          this.logger.write(
            LogLevel.ERROR,
            `Unhandleable message received by inference worker thread: ${JSON.stringify(msg)}`
          );
          break;
      }
    } catch (e) {
      this.logger.write(
        LogLevel.ERROR,
        `Error while handling message: ${e} (message: ${JSON.stringify(msg)})`
      );
    }
  }
}

/**
 * Entry point for the inference worker thread. Sets up listeners and returns.
 */
async function main() {
  // Inside a worker thread.
  if (parentPort === null) {
    // this shouldn't happen...
    console.log("no parent port available");
    return;
  }

  const logger = new WorkerThreadLoggingService(parentPort);
  logger.write(LogLevel.INFO, `Worker ${threadId} has started`);
  const telemetry = new WorkerThreadTelemetryService(parentPort);
  try {
    // Register uncaught exception + unhandled rejection handlers so we can log & send telemetry
    process.on("uncaughtException", (e: Error, s) => {
      if (e instanceof Error) {
        logger.write(LogLevel.ERROR, `Uncaught exception error: ${e.message}`);
        if (e.stack) {
          logger.write(LogLevel.ERROR, `Unhandled exception error stack: ${e.stack}`);
        }
        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_THREAD_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
          [TelemetryPropertyNames.FAULT_TYPE]: "uncaughtException"
        });
      }
      else {
        // this code path shouldn't be reached if everything is properly throwing Errors, but handle this just in case.
        logger.write(LogLevel.ERROR, `Uncaught exception: ${e}`);

        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_THREAD_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: JSON.stringify(e),
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: "none",
          [TelemetryPropertyNames.FAULT_TYPE]: "uncaughtException-incorrectErrorTypeThrown"
        });
      }

    });
    process.on("unhandledRejection", (e: Error, s) => {
      if (e instanceof Error) {
        logger.write(LogLevel.ERROR, `Unhandled rejection error: ${e.message}`);
        if (e.stack) {
          logger.write(LogLevel.ERROR, `Unhandled rejection error stack: ${e.stack}`);
        }

        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_THREAD_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
          [TelemetryPropertyNames.FAULT_TYPE]: "unhandledRejection"
        });
      }
      else {
        // this code path shouldn't be reached if everything is properly throwing Errors, but handle this just in case.
        logger.write(LogLevel.ERROR, `Unhandled rejection exception: ${e}`);

        telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_THREAD_UNHANDLED_FAULT, {
          [TelemetryPropertyNames.EVENT_MESSAGE]: JSON.stringify(e),
          [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: "none",
          [TelemetryPropertyNames.FAULT_TYPE]: "unhandledRejection-incorrectErrorTypeThrown"
        });
      }

    });
    const settingCache = new WorkerThreadSettingsStorage(parentPort);
    const lineCompletionService =
      await createLineCompletionServiceWithRetriesAsync(
        new Settings(settingCache),
        telemetry,
        logger
      );

    new InferenceWorker(
      <MessagePort>parentPort,
      logger,
      lineCompletionService,
      settingCache
    );
  } catch (e) {
    logger.write(LogLevel.FATAL, `Error initializing worker thread: ${e}`);

    if (e instanceof Error) {
      telemetry.sendTelemetryEvent(TelemetryEventNames.WORKER_THREAD_INIT_CRASH, {
        [TelemetryPropertyNames.EVENT_MESSAGE]: e.message,
        [TelemetryPropertyNames.EVENT_EXCEPTION_STACK]: e.stack ?? "null",
        [TelemetryPropertyNames.FAULT_TYPE]: "initCrash"
      });
    }
  }
}

export { main };
