import {
  IExperimentationService,
  IExperimentationTelemetry,
} from "vscode-tas-client";
import { getExperimentationServiceAsync } from "vscode-tas-client";
import * as vscode from "vscode";
import { getTargetPopulation } from "./targetPopulation";

export async function initExperimentationServiceAsync(
  context: vscode.ExtensionContext,
  telemetry: IExperimentationTelemetry
) {
  /**
   * vscode.ExtensionContext according to the .d.ts file does not expose .extension
   * However, it exists and is usable.
   *
   * This may cause problems if vscode changes their API.
   */
  //@ts-ignore
  const { name, version } = context.extension.packageJSON;
  return await getExperimentationServiceAsync(
    name,
    version,
    getTargetPopulation(),
    telemetry,
    context.globalState
  );
}

/**
 * Get the log confidence threshold value from experiment treatment variable
 */
export async function getExperimentalFirstTokenConfidenceThresholdAsync(
  experimentationService: IExperimentationService | undefined,
  defaultValue: number
): Promise<number> {
  if (experimentationService === undefined) {
    return defaultValue;
  }

  const experimentalConfidenceThreshold =
    await experimentationService.getTreatmentVariableAsync(
      "vscode",
      "confidenceThreshold"
    );
  if (
    experimentalConfidenceThreshold === undefined ||
    typeof experimentalConfidenceThreshold !== "string"
  ) {
    return defaultValue;
  }

  const experimentalConfidenceThresholdFloat = parseFloat(
    experimentalConfidenceThreshold
  );
  if (isNaN(experimentalConfidenceThresholdFloat)) {
    return defaultValue;
  }

  const logFirstTokenConfidenceThreshold = Math.log(
    experimentalConfidenceThresholdFloat
  );
  if (isNaN(logFirstTokenConfidenceThreshold)) {
    return defaultValue;
  }

  return logFirstTokenConfidenceThreshold;
}

/**
 * Get whether user should be treated with a reduced set of triggering characters for inline completions
 */
export async function shouldUseExperimentalTriggeringCharactersAsync(
  experimentationService: IExperimentationService | undefined,
  defaultValue: boolean
): Promise<boolean> {
  if (experimentationService === undefined) {
    return defaultValue;
  }

  const shouldUseExperimentalTriggeringCharacters =
    await experimentationService.getTreatmentVariableAsync(
      "vscode",
      "expTriggeringCharacters"
    );

  if (
    shouldUseExperimentalTriggeringCharacters != undefined &&
    typeof shouldUseExperimentalTriggeringCharacters === "boolean"
  ) {
    return shouldUseExperimentalTriggeringCharacters;
  }
  return defaultValue;
}
