//copied from intellicode insiders vscode code base
import * as vscode from "vscode";
import { TargetPopulation } from "vscode-tas-client";


/**
 * Returns a value indicating whether the current VSCode instance is being used by an internal user.
 */
export function isInternal(): boolean {
    const userDomain = process.env.USERDNSDOMAIN ? process.env.USERDNSDOMAIN.toLowerCase() : "";
    return userDomain.endsWith("microsoft.com");
}

/**
 * Returns a value indicating whether the current VS Code instance is insider or not.
 */
export function isInsider(): boolean {
    return vscode.env.appName.indexOf("Insider") > 0;
}

/**
 * Returns a target population for experimentation service.
 */
 export function getTargetPopulation(): TargetPopulation {
    if (isInternal()) {
        return TargetPopulation.Internal;
    }
  
    return TargetPopulation.Public;
}