import * as vscode from "vscode";
import {
  FullyProcessedCompletionData,
  ModifiedCompletionResult,
  PostprocessResults,
} from "../../@types/inlineCompletionProvider";
import { extractCompletionPlaceholder, getPrefix } from "../document";
import {
  removeIndentDedentToken,
  removeOverlappingWhiteSpace,
} from "../postProcRules";

/**
 * This function deals with all the steps necessary to process
 * our completion item fetched from cache as acceptable by VSCode
 * @param codeTillCursor The code in line till the cursor
 * @param codeTillSelectedCompletion The code in line till the selected intellisense completion item
 * @param selectedCompletionText The text of the selected intellisense completion item
 * @param selectedCompletionRange The range of the selected intellisense completion item
 * @param position position passed into inlineCompletionProvider
 * @param completionFromCache CompletionText retrieved from cache
 * @returns Updated completionText accepted by VSCode with additional data to help validation.
 */
export function processCompletion(
  codeTillCursor: string,
  codeTillSelectedCompletion: string | undefined,
  selectedCompletionText: string | undefined,
  selectedCompletionRange: vscode.Range | undefined,
  position: vscode.Position,
  completionFromCache: string
): FullyProcessedCompletionData {
  let fullLineWithCompletion: string | undefined;
  let fullLineWithSnippet: string | undefined;
  let completionToReturn: vscode.SnippetString | undefined;
  let snippetToReturn: string | undefined;
  let postAcceptanceRange: vscode.Range | undefined;
  let completionRange: vscode.Range | undefined;

  const { completionFromCacheText, snippetCompletionText, snippetCount } = postProcess(
    completionFromCache,
    codeTillCursor
  );

  if (!completionFromCacheText) {
    return {
      completionToReturn: new vscode.SnippetString(),
      snippetToReturn: "",
      additionalData: {
        fullLineWithCompletion: "",
        fullLineWithSnippet: "",
        postAcceptanceRange: new vscode.Range(position, position),
        completionRange: new vscode.Range(position, position),
        snippetCount: 0,
        completionFromCacheText: "",
      },
    };
  }

  /**
   * Calculate what the full line would look like w/ the completion
   * Append the prefix to the completion.
   */
  if (selectedCompletionText && selectedCompletionRange) {
    snippetToReturn = selectedCompletionText + snippetCompletionText;

    const endOfIntelliSenseCharNum =
      selectedCompletionRange.start.character + selectedCompletionText.length;

    postAcceptanceRange = new vscode.Range(
      new vscode.Position(
        selectedCompletionRange.start.line,
        endOfIntelliSenseCharNum
      ),
      new vscode.Position(
        selectedCompletionRange.start.line,
        endOfIntelliSenseCharNum + completionFromCacheText.length
      )
    );

    /**
     * 3 part scenario) IntelliSense active
     *   Text before IntelliSense
     *   + IntelliSense selection
     *   + WLC suggestion after IntelliSense
     */
    fullLineWithCompletion =
      codeTillSelectedCompletion + selectedCompletionText + completionFromCacheText;
    fullLineWithSnippet =
      codeTillSelectedCompletion + selectedCompletionText + snippetCompletionText;
  } else {
    /* Append the prefix to the suggestion */
    const prefix = getPrefix(codeTillCursor);
    snippetToReturn = prefix + snippetCompletionText;
    postAcceptanceRange = new vscode.Range(
      new vscode.Position(position.line, position.character - prefix?.length),
      new vscode.Position(
        position.line,
        position.character + completionFromCacheText.length
      )
    );

    /**
     * 2 part scenario) No IntelliSense
     *   Text before cursor
     *   + WLC suggestion after cursor
     */
    fullLineWithCompletion = codeTillCursor + completionFromCacheText;
    fullLineWithSnippet = codeTillCursor + snippetCompletionText;
  }
  const modifiedCompletion = getCompletionToReturnAndRange(codeTillCursor, position.line, fullLineWithSnippet);
  completionToReturn = new vscode.SnippetString(modifiedCompletion.text);
  completionRange = modifiedCompletion.range;

  return {
    completionToReturn,
    snippetToReturn,
    additionalData: {
      fullLineWithCompletion,
      fullLineWithSnippet,
      postAcceptanceRange,
      completionRange,
      snippetCount,
      completionFromCacheText
    },
  };
}

/**
 * Certain steps need to be done after results are pulled out of cache.
 * This function takes care of those steps.
 *
 * Our cache will return only the part that needs to be appended after the cursor.
 * For example, it'll only return `sole.log` when the cursor is after `con`.
 *
 * @param completion Suggestions as returned by cache
 * @param codeTillCursor Code before the suggesting  on that line
 * @returns Post processed completions that can be returned to VSCode
 */
function postProcess(
  completion: string,
  codeTillCursor: string
): PostprocessResults {
  const inProcess = removeIndentDedentToken(
    removeOverlappingWhiteSpace(codeTillCursor, completion)
  ).replace(/\r?\n/g, "");

  const completionWithoutSnippet = extractCompletionPlaceholder(
    inProcess,
    false
  ).completionString;
  const { completionString: completionWithSnippet, snippetCount } =
    extractCompletionPlaceholder(inProcess, true);

  return {
    completionFromCacheText: completionWithoutSnippet,
    snippetCompletionText: completionWithSnippet,
    snippetCount,
  };
}

/**
 * Calculates the completion to be returned and range to be replaced
 * @param codeTillCursor The text on the current line till the cursor
 * @param currLineNumber Current line number of cursor
 * @param fullLineWithCompletion Full line with completion
 * @returns Remaining prediction + range
 */
export function getCompletionToReturnAndRange(
  codeTillCursor: string,
  currLineNumber: number,
  fullLineWithCompletion: string
): ModifiedCompletionResult {

  /**
   * If the codeTillCursor has additional whitespaces than the full line with completion,
   * we want to add those spaces to the full line with completion and modify insert text and range
   */
  for (let i = 0; i < codeTillCursor.length; i++) {
    if (codeTillCursor[i] == " " && codeTillCursor[i] != fullLineWithCompletion[i]) {
      fullLineWithCompletion = [fullLineWithCompletion.slice(0, i), " ", fullLineWithCompletion.slice(i),].join("");
    }
  }

  const modifiedResult: ModifiedCompletionResult = {
    text: fullLineWithCompletion,
    range: new vscode.Range(
      new vscode.Position(currLineNumber, 0),
      new vscode.Position(currLineNumber, fullLineWithCompletion.length)
    ),
  };
  return modifiedResult;
}

export const exportedMethodsForTesting = {
  postProcess
}
