import { SelectedCompletionInfo } from "vscode";
import { isAlphanumeric } from "../document";
/**
 * User typed character which triggers a completion
 */
const triggeringCharacters = new Set<string>([
  ".",
  "=",
  ",",
  "+",
  " ",
  "[",
  "]",
  "(",
  ")",
  "{",
  "}",
  "\n",
  "\t",
]);

enum triggeringScenario {
  INTELLISENSE_DRIVEN = "IntellisenseDriven", 
  TAB_TO_ACCEPT = "TabToAccept",
  WHITESPACE_NEW_LINE = "WhitespaceNewLine",
  WHITESPACE_TAB = "WhitespaceTab",
  WHITESPACE = "Whitespace"
}

/**
 * Determines if a prediction is to be triggered or not
 * Triggering conditions:
 * User typed a triggering character
 * Intellisense item selected
 * Explicit invocation
 * Tab to accept
 * @param codeBefore code before the cursor
 * @param selectedIntellisenseCompletion item selected in the intellisense menu
 * @param codeOnLine code on the current line
 * @param codeTillCursor code on the current line till the cursor
 * @returns triggering character or event
 */
export function getTriggerNew(
  codeBefore: string,
  selectedIntellisenseCompletion: SelectedCompletionInfo | undefined,
  codeOnLine: string,
  codeTillCursor: string
): string | undefined {
  const charBeforeCursor = codeBefore.charAt(codeBefore.length - 1);
  // If the character before the cursor belongs to the set of triggering characters
  if (triggeringCharacters.has(charBeforeCursor)) {
    if (["\n", "\r\n", "\r"].includes(charBeforeCursor)){
      return triggeringScenario.WHITESPACE_NEW_LINE;
    }
    if (charBeforeCursor === "\t"){
      return triggeringScenario.WHITESPACE_TAB;
    }
    if (charBeforeCursor === " "){
      return triggeringScenario.WHITESPACE;
    }
    return charBeforeCursor;
  }
  // If an item is selected in the intellisense menu
  if (selectedIntellisenseCompletion != undefined) {
    return triggeringScenario.INTELLISENSE_DRIVEN;
  }
  // If cursor is at the end of line
  // This is a heuristic to determine if the user tabbed to accept a prediction
  if (codeOnLine == codeTillCursor) {
    return triggeringScenario.TAB_TO_ACCEPT;
  }
  return undefined;
}

/**
 * All non-alphanumeric characters are considered triggering characters
 * @param codeBefore code before the cursor
 * @param selectedIntellisenseCompletion item selected in the intellisense menu
 * @returns triggering character or event
 */
export function getTriggerOld(
  codeBefore: string,
  selectedIntellisenseCompletion: SelectedCompletionInfo | undefined
): string | undefined {
  const charBeforeCursor = codeBefore.charAt(codeBefore.length - 1);

  // If char is non alpha numeric
  if (!isAlphanumeric(charBeforeCursor.charCodeAt(0))) {
    if (["\n", "\r\n", "\r"].includes(charBeforeCursor)){
      return triggeringScenario.WHITESPACE_NEW_LINE;
    }
    if (charBeforeCursor === "\t"){
      return triggeringScenario.WHITESPACE_TAB;
    }
    if (charBeforeCursor === " "){
      return triggeringScenario.WHITESPACE;
    }
    return charBeforeCursor;
  }

  // If an item is selected in the intellisense menu
  if (selectedIntellisenseCompletion != undefined) {
    return triggeringScenario.INTELLISENSE_DRIVEN;
  }

  return undefined;
}
